
body {
  font-family: 'Arial', sans-serif;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
}

.app-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

header {
  background-color: #3b389a;
  color: white;
  padding: 20px 0;
  text-align: center;
  margin-bottom: 20px;
  border-radius: 40px;
}

header h1 {
  font-size: 2.5rem;
  margin: 0;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.add-item {
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.add-item form {
  display: flex;
  align-items: center;
}

.add-item input[type="text"] {
  flex-grow: 1;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.add-item button {
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  background-color: #00FF00;
  color: #320808;
  border: none;
  border-radius: 3px;
  margin-left: 10px;
}

.add-item button:hover {
  background-color: #2ab300;
}

.item-list-container {
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.item-list-container h2 {
  font-size: 1.8rem;
  margin-bottom: 15px;
  text-align: center;
}

.item-list-scrollable {
  max-height: 400px; /* Adjust as needed */
  overflow-y: auto;
  border-top: 1px solid #ccc; /* Optional: Add border to separate header from list */
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  margin-bottom: 10px;
  padding: 10px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background-color 0.3s ease;
}

li:hover {
  background-color: #f0f0f0;
}

.item-actions button {
  margin: 6px;
  padding: 8px 12px;
  font-size: 1rem;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  transition: background-color 0.3s ease;
}

.item-actions .edit:hover {
  background-color: #0056b3;
}

.item-actions .del:hover {
  background-color: #f63f3f;
}

.update-item {
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.update-item form {
  display: flex;
  align-items: center;
}

.update-item input[type="text"] {
  flex-grow: 1;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.update-item button {
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  margin-left: 10px;
}

.update-item button:hover {
  background-color: #0056b3;
}
/* UpdateItem.css */

.update-item-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.update-item-modal-content {
  background-color: #fff;
  width: 80%;
  max-width: 600px;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  position: relative;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  color: #999;
}

.close-btn:hover {
  color: #555;
}

.update-item-modal-content h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.update-item-modal-content form {
  display: flex;
  flex-direction: column;
}

.update-item-modal-content form input {
  padding: 10px;
  margin-bottom: 15px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.update-item-modal-content form button {
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  transition: background-color 0.3s ease;
}

.update-item-modal-content form button:hover {
  background-color: #0056b3;
}


.fade-in {
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
